<script>
export default{
    name: 'Main',
    data(){
        return{
            beginningWordsOpNumber:null
        }
    },
    mounted(){
        // beginning words initially set to 0
        this.beginningWordsOpNumber = 0
    },
    methods:{
        changeWords(){
            // size of the biginning words are 3 so far
            var size = 3
            var beginningWordsList = ["beginningWordsOp1","beginningWordsOp2","beginningWordsOp3"]
            if (this.beginningWordsOpNumber == 2){
                this.beginningWordsOpNumber = 0;
            } else {
                this.beginningWordsOpNumber += 1;
            }
            // first let all the option become invisible
            for(var i=0;i<size;i++){
                document.getElementById(beginningWordsList[i]).style.display='none'
            }
            // then set one of option become visible
            document.getElementById(beginningWordsList[this.beginningWordsOpNumber]).style.display='block'
        }
    }
}
</script>


